import React,{Component} from "react";
import  "./Home.css";

class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      text  : ""
    }
  };

  componentDidMount() {
    let i = 0;

    const typing = setInterval(() => {
      const typed = this.state.text;
      const finalText = "저의 웹 개발 포트폴리오를 소개합니다.";

      if(i < finalText.length) { 
        this.setState({
          text  : typed + finalText[i]
        });

        i++;

      } else {
        setTimeout(() => {
          this.setState({
            text  : ""
          });

          i = 0;
        }, 1500);
      }
    }, 150);
  }

  render() {
    return (
      <div id="h" className="typer">
        <h1 className="typer-text">{this.state.text}</h1>
      </div>
    );
  }
}

export default Home;