import React, { Component } from 'react';
import { Redirect } from 'react-router';
import  './About.css';

class About extends Component {

  render() {
    return (
      <div className="about_me" id="a" >
        <div className="about_me_title">
          <h1 className="hedding">자기소개</h1>
        </div>
        <div className="about_me_content">
          <h1>"이 분야에 <span style={{color: 'red'}}>진짜로</span> 미친 놈이 바로 <span style={{color: 'red'}}>나야</span>"</h1>
          <section className="about_me_section">
            <p>
            소위 어떤 분야에 미친다는 것은 그 분야의 전문가가 되기 위한 가장 좋은 방법입니다. 그러나 어떠한 목적이나 동기도 없이 한 분야에 미치기는 어려울 것입니다.
            <br /><br />
            초등학생 때, 부모님의 권유로 특별한 목적없이 3년간 피아노를 배우며 콩쿨 대회에서 입상까지 하였습니다. 그러나 지금은 그 감각과 실력이 거의 남아있지 않습니다.
            반면, 풋살은 대학생이 되서야 시작했음에도 현재 수준급의 실력을 갖추고 있으며 제가 속해있던 팀이 '부천시 아마추어 풋살 대회'에서 3위에 입상할 정도로 많은 열정을 
            갖고 활동했었습니다. 이 차이는 '내가 정말로 좋아하는 취미를 찾아 거기에 미쳐보고 싶다.' 는 목표설정과 동기부여의 유무에 따른 결과였다고 생각합니다.
            <br /><br />
            한편, 제 인생의 가장 큰 목표는 '나에게 딱 맞는 일을 찾아 죽을 때까지 미쳐보자.' 입니다. 다행인 것은 단순히 코딩이 재밌다는 이유로 선택했던 이 프로그래머의 길이 그 
            목표를 달성하는데 안성맞춤이였다는 것입니다. 저는 이미 '새로운 것들을 개발해내며 보람을 느끼는 것', '다음에는 어떤 새로운 것들을 개발해낼지 다시금 기대하는 것' 과 
            같은 재미에 흠뻑 빠져있습니다. 그렇기에 스스로 프로그래머의 길을 자처하며 끊임없이 다양한 프로젝트를 진행해 왔습니다. 앞으로도 오랜시간동안 이 길을 계속 걸으며 죽을 
            때까지 이 분야에 미쳐보고 싶습니다.
            </p>
          </section>
        </div>
      </div>
    );
  }
}


export default About;